import { createStore, applyMiddleware, compose } from 'redux';
import Immutable from 'seamless-immutable';
import createSagaMiddleware from 'redux-saga';

import rootReducer from './reducers/index';
import rootSaga from './sagas/index';

const sagaMiddleware = createSagaMiddleware();
const dev = process.env.NODE_ENV !== 'production';
const windowExist = typeof window === 'object';

/* eslint-disable no-underscore-dangle */
const composeEnhancers
  = dev && windowExist && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    : compose;

/* eslint-enable */

export const setupRedux = (initialState = {}) => createStore(
  rootReducer(),
  initialState,
  composeEnhancers(applyMiddleware(sagaMiddleware))
);

export function configureStore(initialState = {}) {
  /* eslint-disable no-param-reassign */
  if (!initialState.toJS) {
    initialState = Immutable(initialState);
  }

  const store = createStore(
    rootReducer(),
    initialState,
    composeEnhancers(applyMiddleware(sagaMiddleware))
  );

  store.sagaTask = sagaMiddleware.run(rootSaga);
  return store;
}

export function withReduxSaga(BaseComponent) {
  return (configureStore)(BaseComponent);
}



