import { all, call, put, takeLatest, takeEvery } from 'redux-saga/effects';

import { request } from '../utils/request';
import { mygramUrl } from '../utils/BaseUrl';

import actionTypes, {
  mygramFailure,
  loadMygramSuccess,
} from '../actions/mygramAction';

function* loadMygram({ token }) {
  const requestURL = mygramUrl;

  try {
    const data = yield call(request, requestURL, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });
    yield put(loadMygramSuccess(data));
  } catch (err) {
    const res = yield err.response.json();
    yield put(mygramFailure(res.message));
  }
}

function* mygramWatcher() {
  yield all([
    takeLatest(actionTypes.LOAD_MYGRAM, loadMygram),
  ]);
}

export default mygramWatcher;



